import React from 'react';
import '../App.css';
import {Summary,summ} from './summary';

class Checkout extends React.Component{

    state = {
        mess: '',
        orderCreate: false,
    }

    setRequest = (e) => {
        e.preventDefault()

        //   
        if(
            this.state.username === undefined
            || this.state.useremail === undefined
            || this.state.userphone === undefined){
                this.setState({mess: '  '});
                return;
        }

        let json = createRequestData(this.props.data, this.state);
        let svgs = this.props.svgs;
        let url = this.props.data.importOptions.REQUEST_URL;
        let formData = new FormData();
        formData.append('order', json);
        formData.append('svg', JSON.stringify(svgs));
        formData.append('mailto', this.props.data.importOptions.MAILTO);
        formData.append('mailfrom', this.props.data.importOptions.MAIL_FROM);
        formData.append('subtotal_table_data', summ(this.props.data,this.props.data.importOptions,false,true));
        this.setState(
            {mess: ' ...'},
            () => fetch(url, { method: 'POST', body: formData })
                .then((res) => res.text())
                .then(
                    (res) => {
                        if(res === 'ok'){
                            let mess = '    .   20 ( )             .'
                            this.setState(
                                {mess,orderCreate:true},
                                () => {
                                    this.props.afterCreateOrderFunc();
                                }
                            );
                        }
                        else{
                            this.setState({mess: '   .   .'});
                        }
                    },
                    (error) => {
                        this.setState({mess: '    #' + error});
                    }
                )
        )
        
    }

    heandleChange = (e) => {
        let target = e.target;
        let fieldName = target.name;
        const value = target.type === 'checkbox' ? target.checked : target.value;

        this.setState(
            {[fieldName]: value, mess: ''},
            () => {
                if(fieldName === 'faster' || fieldName === 'inbox' || fieldName === 'poddon')
                    this.props.setOrderDataFunc(fieldName, this.state[fieldName]);
            }
        );        
    }

    render(){
        const data = this.props.data;
        const billing = data.billingOptions;
        let message = this.state.mess ? <div id="message">{this.state.mess}</div> : '';
        if(!this.state.orderCreate){
        return(
            <>
            <Summary set={data} options={data.importOptions} />
            <h4 className="tac">  </h4>
            <form onSubmit={this.setRequest} id="checkoutForm">
                <fieldset className="flex w100p">
                    <legend> </legend>
                    <label className="reqiured-field">
                        <input type="text" name="username" required defaultValue={this.state.username} placeholder="" onChange={this.heandleChange} />
                    </label>
                    <label className="reqiured-field">
                        <input type="phone" name="userphone" required defaultValue={this.state.userphone} placeholder="" onChange={this.heandleChange} />
                    </label>
                    <label className="reqiured-field">
                        <input type="email" name="useremail" required defaultValue={this.state.useremail} placeholder=" email" onChange={this.heandleChange} />
                    </label>
                    <label className="">
                        <textarea name="usercomment" defaultValue={this.state.usercomment} placeholder="  " onChange={this.heandleChange}></textarea>
                    </label>
                </fieldset>
                <fieldset className="flex w100p">
                    <legend></legend>
                    <label className="flex-center">
                        <span className="fons12"> </span>
                        <input type="checkbox" name="faster" checked={billing.faster} onChange={this.heandleChange} />
                    </label>
                    <label className="flex-center">
                        <span className="fons12">  </span>
                        <input type="checkbox" name="inbox" checked={billing.inbox} onChange={this.heandleChange} />
                    </label>
                    <label className="flex-center">
                        <span className="fons12">  </span>
                        <input type="checkbox" name="poddon" checked={billing.poddon} onChange={this.heandleChange} />
                    </label>
                </fieldset>
                <fieldset className="w100p">
                    <legend></legend>
                    <label>
                        <input type="text" name="billingaddress" defaultValue={this.state.billingaddress} placeholder=" " onChange={this.heandleChange} />
                    </label>
                </fieldset>
            </form>
            <div id="calc-form-controlls-wrapper">
                <button className="btn" onClick={this.props.toOrderFunc}>  </button>
                <button className="btn" onClick={this.setRequest}> </button>
            </div>
            {message}
            </>
        );
        }
        else{
            return(
                <>
                <div id="calc-form-controlls-wrapper">
                    <button className="btn" onClick={this.props.toOrderFunc}>  </button>
                </div>
                {message}
                </>
            );
        }
    }
}

export {Checkout};

function createRequestData(data,form){
    let details = data.details;
    let o = data.importOptions;
    let date = new Date();
    let userData = `${form.username} ${form.userphone} ${form.useremail}`;
    let space = '';
    let order = [
        [space],
        ['',userData,space,space,space,'',formatDate(date)],
        [space],
        [' ',o.SHEET_COLOR,space,' ',o.SHEET_DEPTH],
        [space],
        [' ',space,'',space],
        [' ',space,'',space],
        [' ',space,'',space],
        [space],
        ['','','','-',' L1',' L2',' W1',' W2','',''],
        [space]
    ]
    //console.log(order);
    let ind = 1;
    details.forEach((d,key) => {
        let textureRotate = d.textureRotate;
        let width = d.nominalwidth;
        let length = d.nominalheight;
        let edgeL1 = d.edge.left > 0 ? d.edge.left : '';
        let edgeL2 = d.edge.right > 0 ? d.edge.right : '';
        let edgeW1 = d.edge.top > 0 ? d.edge.top : '';
        let edgeW2 = d.edge.bot > 0 ? d.edge.bot : '';
        //   ,   -
        if(textureRotate === 0){
            width = d.nominalheight;
            length = d.nominalwidth;
        }
        //   ,    L-W
        if(textureRotate === 0){
            edgeL1 = d.edge.top;
            edgeL2 = d.edge.bot;
            edgeW1 = d.edge.right;
            edgeW2 = d.edge.left;
        }
        //    
        let row = [
            `${ind}`, // key 
            length,
            width,
            d.count,
            edgeL1,edgeL2,edgeW1,edgeW2,
            _e(d.groove ? d.groove : ''),
            createComment(d,form)
        ];
        order.push(row);
        ind++;
    })
    return JSON.stringify(order);
}

function createComment(d,form){
    let comment = '';
    let breakString = '\n';
    //  
    if(d.lt.x > 0 && d.lt.y > 0)
        comment += ` : ${_e(d.lt.type)} | X:${d.lt.nomX} Y:${d.lt.nomY}` + breakString;
    if(d.rt.x > 0 && d.rt.y > 0)
        comment += ` : ${_e(d.rt.type)} | X:${d.rt.nomX} Y:${d.rt.nomY}` + breakString;
    if(d.rb.x > 0 && d.rb.y > 0)
        comment += ` : ${_e(d.rb.type)} | X:${d.rb.nomX} Y:${d.rb.nomY}` + breakString;
    if(d.lb.x > 0 && d.lb.y > 0)
        comment += ` : ${_e(d.lb.type)} | X:${d.lb.nomX} Y:${d.lb.nomY}` + breakString;
    //  
    if(d.loops && d.loops.side !== 'none'){
        comment += `- : ${d.loops.count}` + breakString;
        comment += `: ${_e(d.loops.side)}` + breakString;
        comment += ` : ${d.loops.beforeNominal}` + breakString;
        comment += ` : ${d.loops.afterNominal}` + breakString;
    }
    //  .
    if(form.faster)
        comment += '  - ' + breakString;
    if(form.inbox)
        comment += '   - ' + breakString;
    if(form.poddon)
        comment += '   - ' + breakString;
    //  
    if(d.comment)
        comment += '  :' + d.comment + breakString;

    return comment;
}

function formatDate(date) {
    var dd = date.getDate();
    if (dd < 10) dd = '0' + dd;
  
    var mm = date.getMonth() + 1;
    if (mm < 10) mm = '0' + mm;
  
    var yy = date.getFullYear() % 100;
    if (yy < 10) yy = '0' + yy;
  
    return dd + '.' + mm + '.' + yy;
}

function _e(text){
    // Sides
    if(text === 'left')
        return '';
    else if(text === 'top')
        return '';
    else if(text === 'right')
        return '';
    else if(text === 'bot')
        return '';
    // Angles
    else if(text === 'outside')
        return '';
    else if(text === 'linear')
        return '';
    else if(text === 'inside')
        return '';
    // Default
    else
        return text;
}