import '../App.css';

function TextureVector(props){

    let {width,height,textureRotate} = props.set;
    let m = 70;
    let arrows  = '>>>>>';
    let x = textureRotate === 0 ? (70+width)/2 - 40  : (70+width)/2 + 60 ;
    let y = textureRotate === 0 ? (70+height)/2 + 40 : (70+height)/2 + 100 ;
    return(
        <text 
            fontSize="400%" 
            fill="#999999"
            transform={`rotate(${-textureRotate},${x},${y})`} 
            x={x} 
            y={y}>
                {arrows}
        </text>
    )
}

export {TextureVector};