import React from 'react';
import '../App.css';

function getCount(set,o){
    let listS = o.SHEET_WIDTH*o.SHEET_LENGHT*o.SHEET_FILLING_AREA;
    let S = 0;
    let listCount = set.size > 0 ? 1 : 0;
    let detailsCount = 0;
    for (let detail of set.values()) {
        let width = detail.nominalwidth;
        let height = detail.nominalheight;
        detailsCount += detail.count;
        S += width*height*detail.count; //      
    }
    //  - 
    listCount += Math.floor(S/listS);

    return {
        lists: listCount,
        details: detailsCount
    };
}

function Summary(props){

    let set = props.set.details;
    let o = props.options;
    let summaryOut = [];
    let summary = {
        '': o.SHEET_NAME,
        '': `${o.SHEET_WIDTH} x ${o.SHEET_LENGHT} x ${o.SHEET_DEPTH}`,
        '  ': getCount(set,o).lists,
        '  ': `${o.SHEET_PRICE} .`,
        ' ': getCount(set,o).details
    }
    let materialImageUrl = 'http://rondo.96min.ru/' + window.importOptions.SHEET_PICTURE_URL;

    for(let key in summary){
        summaryOut.push(<SummItem key={key} title={key} value={summary[key]} />);
    }
    
    return(
        <div id="summary" style={{backgroundImage:"url(" + (materialImageUrl) + ")"}}>
            {summaryOut}
            <div className="summ-item">
                <span><b> :</b></span>
                <span><b>{summ(props.set,o,true)}</b></span>
            </div>
            <MoreOrderDetails set={props.set} o={o} />
        </div>
    )
}

export {Summary};

class MoreOrderDetails extends React.Component{

    state = {
        open: false,
    }

    render(){
        const summNumber = summ(this.props.set, this.props.o);
        const detailsListSize = this.props.set.details.size;
        if(detailsListSize === 0)
            return null;
        else if(this.state.open)
            return(
                <div className="summ-item more">
                    <span onClick={() => this.setState({open:false})} className="action-link red w100p"><b></b></span>
                    {summNumber}
                </div>
            );
        else
            return(
                <div className="summ-item">
                    <span onClick={() => this.setState({open:true})} className="action-link red"><b>  </b></span>
                </div>
            );
    }
}



function SummItem(props){

    return(
        <div className="summ-item">
            <span>{props.title}: </span>
            <span>{props.value}</span>
        </div>
    )
}

function summ(set,o,final=false,get=false){
    let listS = o.SHEET_WIDTH*o.SHEET_LENGHT*o.SHEET_FILLING_AREA;
    let S = 0;
    let listCount = set.details.size > 0 ? 1 : 0;
    let edge = {sm:0, lg:0}
    let angle = 0;
    let groove = 0;
    let loops = 0;
    let billing = set.billingOptions ? set.billingOptions : false;
    for (let detail of set.details.values()) {
        let width = detail.nominalwidth;
        let height = detail.nominalheight;
        let detailsCount = detail.count;

        S += width*height*detailsCount; //      
        
        // 
        for(let edgeSide in detail.edge){ 
            let edgeSize = detail.edge[edgeSide];
            if( edgeSize > 0 && edgeSize < 1){
                edgeSide === 'left' || edgeSide === 'right' ? edge.sm += height : edge.sm += width;
            }
            else if(edgeSize > 0 && edgeSize > 1){
                edgeSide === 'left' || edgeSide === 'right' ? edge.lg += height : edge.lg += width;
            }
        }
        // 
        if(detail.lt.x > 0 || detail.rt.x > 0 || detail.rb.x > 0 || detail.lb.x > 0)
            angle++;
        // 
        if(detail.loops && detail.loops.side !== 'none')
            loops += detail.loops.count;
        // 
        if(detail.groove)
            groove++;
        
    }

    //  - 
    listCount += Math.floor(S/listS);

    let edgeSM = edge.sm*o.EDGE_MULTIPLICATOR/1000*Math.abs(o.PRICE_EDGE_04);
    let edgeLG = edge.lg*o.EDGE_MULTIPLICATOR/1000*Math.abs(o.PRICE_EDGE_02);
    let edgeSumm = Math.round(edgeSM+edgeLG+((edge.sm+edge.lg)*o.EDGE_MULTIPLICATOR/1000*Math.abs(o.PRICE_EDGING)));
    let material = listCount*o.SHEET_PRICE;
    let work = listCount*o.PRICE_SHEET_CUTTING;
    let angles = angle*Math.abs(o.PRICE_CURVATURE);
    let finalLoops = loops*Math.abs(o.PRICE_LOOP);
    let finalGrooves = groove*Math.abs(o.PRICE_GROOVE);
    let pack = billing && billing.inbox && listCount > 0 ? +o.PRICE_TAPE : 0;
    let pallet = billing && billing.poddon && listCount > 0 ? +o.PRICE_PALLET : 0;
    let finalSumm = material+work+edgeSumm+angles+finalLoops+finalGrooves+pack+pallet;
    
    // Billing options
    let billingOptions = [];
    if(billing){
        if(billing.faster)
            billingOptions.push(<tr><td> </td><td>{price((+o.PRICE_PERCENT_FAST - 1)*finalSumm)}</td></tr>);
        if(billing.inbox)
            billingOptions.push(<tr><td></td><td>{price(+o.PRICE_TAPE)}</td></tr>);
        if(billing.poddon)
            billingOptions.push(<tr><td> </td><td>{price(+o.PRICE_PALLET)}</td></tr>);
    }
    
    if(billing && billing.faster)
            finalSumm *= +o.PRICE_PERCENT_FAST;
    
    if(final)
        return price(finalSumm);
    
    if(get){
        let out = [
            ["", o.SHEET_NAME],
            ["", `${o.SHEET_WIDTH} x ${o.SHEET_LENGHT}`],
            ["  ", o.SHEET_PRICE],
            ["  ", listCount],
            [" ", getCount(set.details,o).details],
            ['<b></b>', `<b>${price(finalSumm)}</b>`],
            ['', false],
            ['', price(material)],
            ['', price(work)],
            ['', price(edgeSumm)],
            ['', price(angles)],
            ['', price(finalLoops)],
            ['', price(finalGrooves)],
        ];
        if(billing){
            if(billing.faster)
                out.push([' ', price((+o.PRICE_PERCENT_FAST - 1)*finalSumm)]);
            if(billing.inbox)
                out.push(['',price(+o.PRICE_TAPE)]);
            if(billing.poddon)
                out.push([' ',price(+o.PRICE_PALLET)]);
        }
        return JSON.stringify(out);
    }
    
    return(
        <table className="w100p">
            <tbody>
                <tr><td></td><td>{price(material)}</td></tr>
                <tr><td></td><td>{price(work)}</td></tr>
                <tr><td></td><td>{price(edgeSumm)}</td></tr>
                <tr><td></td><td>{price(angles)}</td></tr>
                <tr><td></td><td>{price(finalLoops)}</td></tr>
                <tr><td></td><td>{price(finalGrooves)}</td></tr>
                {billingOptions}
            </tbody>
        </table>
    );
}
export {summ};

function price(number){
    return (number).toFixed(1).replace(/\d(?=(\d{3})+\.)/g, '$&,')  + ' .';
}