import React from 'react';
import '../App.css';
import {Helper} from '../helper';

const helper = new Helper();

// ============================================ LOOPS
class LoopsOptions extends React.Component{
    state = {
      side: 'none',
      count: 2,
      before: 100,
      after: 200,
      minSpace: 50,
      error: false,
    }
  
    handleInputChange = (e) => {
        let fieldName = e.target.name; //  
        let value = e.target.value; //  
        let key = e.target.closest('.detail').dataset.key; //  
        let steps = this.state.count - 1; //  -      
        let step = Math.round((this.state.after-this.state.before)/steps, 1); //   
        let set = this.props.set;
        //    
        if(e.target.type === 'number'){
            //const min = e.target.min;
            const max = e.target.max;
            if(+value > +max) value = max;
        }
        
        this.setState({
            [fieldName]: value,
            error: false
        }, () => {
            //      , 
            //           ,
            //    minSpace,    indent(    )
            if(fieldName === 'count' && step < this.state.minSpace){
                return
            }
            let before = helper.enscalingAny(this.state.before, set, this.state.side);
            let after = helper.enscalingAny(this.state.after, set, this.state.side);
            let loops = {
                side: fieldName === 'side' ? value : this.state.side,
                count: this.state.count,
                before: before,
                beforeNominal: this.state.before,
                after: after,
                afterNominal: this.state.after,
                minSpace: this.state.minSpace,
            }
            this.props.func(loops, key); //    
        })
    }

    isFree = (r,A1,A2) => {
        if(r === 'groove' || A1 || A2){
            return true;
        }
        if(!r) return false;
    }
  
    render(){
        let {side,before,after,minSpace} = this.state;
        let set = this.props.set;
        let beforeMin = +minSpace*2;
        let beforeMax = after - minSpace*2;
        let afterMax = side === 'top' || side === 'bot' ? set.nominalwidth - minSpace*2 : set.nominalheight - minSpace*2;
        let afterMin = +minSpace*2 + +before;
        let countMax = Math.round((after-before)/minSpace) + 1;
        let LTangle = +set.lt.x === 0 ? false : true;
        let RTangle = +set.rt.x === 0 ? false : true;
        let RBangle = +set.rb.x === 0 ? false : true;
        let LBangle = +set.lb.x === 0 ? false : true;
        let r = set.reserve;
        let resL = this.isFree(r.left,LTangle,LBangle);
        let resT = this.isFree(r.top,LTangle,RTangle);
        let resR = this.isFree(r.right,RTangle,RBangle);
        let resB = this.isFree(r.bot,RBangle,LBangle);
        
        if(set.nominalwidth < 150)
            resT = resB = true;
        if(set.nominalheight < 150)
            resL = resR = true;

        if(r.left==='groove' || r.top==='groove' || r.right==='groove' || r.bot==='groove'){
            return(
                <span className="fons12 red">   </span>
            )
        }
        if(resL && resT && resR && resB){
            return(
                <span className="fons12 red"> </span>
            )
        }

        return(
            <>
            <label className="flex-center">
                <span></span>
                <select onChange={this.handleInputChange} name="side" defaultValue="none" value={set.loops.side}>
                    <option key="loopnone" value="none"></option>
                    <option key="loopleft" value="left"  disabled={resL}>{resL && ' ()'}</option>
                    <option key="looptop" value="top"   disabled={resT}>{resT && ' ()'}</option>
                    <option key="loopright" value="right" disabled={resR}>{resR && ' ()'}</option>
                    <option key="loopbot" value="bot"   disabled={resB}>{resB && ' ()'}</option>
                </select>
            </label>
            <label className="flex-center ml-a"><span className="fons12"> /    {beforeMin}.</span></label>
            <div className="w100p"></div>

            {this.state.side !== 'none' &&
            <>
            <label className="flex-center">
                <span>&nbsp; <br/>, </span>
                <input 
                    onInput={this.handleInputChange} 
                    name="before" 
                    type="number"
                    min={beforeMin}
                    max={beforeMax}
                    step="1" 
                    value={this.state.before.toString()} />
                    <span>max: <b>{beforeMax}</b>&nbsp;</span>
            </label>
            <label className="flex-center">
                <span>&nbsp; <br/>, </span>
                <input 
                    onInput={this.handleInputChange} 
                    name="after" 
                    type="number"
                    min={afterMin}
                    max={afterMax}
                    step="1" 
                    value={this.state.after.toString()} />
                    <span>max: <b>{afterMax}</b>&nbsp;</span>
            </label>
            <label className="flex-center">
                <span>-, </span>
                <input 
                    className={this.state.error ? 'error' : ''}
                    onInput={this.handleInputChange} 
                    name="count" 
                    type="number"
                    min="2"
                    max={countMax}
                    step="1" 
                    defaultValue="2" />
            </label>
            </>
            }
            </>
        );
    }
  }

function Loops(props){
    let set = props.set;
    let m = props.m;
    let loops = set.loops;
    let indent = helper.enscalingAny(25,set,loops.side);
    let radius = helper.enscalingAny(35/2,set,loops.side);

    if(loops.side === undefined || loops.side === 'none') return('');
  
    let width = set.width;
    let height = set.height;
    let circles = [];

    if(loops.side === 'left'){
        circles.push(<path key="lpath" d={`M ${m+indent} ${m} v ${height}`} stroke="#72aee6" strokeDasharray="10" fill="transparent"/>);
        circles.push(<Circle set={set} key={'lbefore'} side={loops.side} nominal={+loops.beforeNominal} cx={m+indent} cy={+loops.before+m} r={radius} m={m} />);
        circles.push(<Circle set={set} key={'lafter'} side={loops.side} nominal={+loops.afterNominal} cx={m+indent} cy={+loops.after+m} r={radius} m={m} />);
    }
    else if(loops.side === 'top'){
        circles.push(<path key="tpath" d={`M ${m} ${m+indent} h ${width}`} stroke="#72aee6" strokeDasharray="10" fill="transparent"/>);
        circles.push(<Circle set={set} key={'tbefore'} side={loops.side} nominal={+loops.beforeNominal} cx={+loops.before+m} cy={m+indent} r={radius} m={m} />);
        circles.push(<Circle set={set} key={'tafter'} side={loops.side} nominal={+loops.afterNominal} cx={+loops.after+m} cy={m+indent} r={radius} m={m} />);
    }
    else if(loops.side === 'right'){
        circles.push(<path key="rpath" d={`M ${m+width-indent} ${m} v ${height}`} stroke="#72aee6" strokeDasharray="10" fill="transparent"/>);
        circles.push(<Circle set={set} key={'rbefore'} side={loops.side} nominal={+loops.beforeNominal} cx={m+width-indent} cy={+loops.before+m} r={radius} m={m} />);
        circles.push(<Circle set={set} key={'rafter'} side={loops.side} nominal={+loops.afterNominal} cx={m+width-indent} cy={+loops.after+m} r={radius} m={m} />);
    }
    else if(loops.side === 'bot'){
        circles.push(<path key="bpath" d={`M ${m} ${height+m-indent} h ${width}`} stroke="#72aee6" strokeDasharray="10" fill="transparent"/>);
        circles.push(<Circle set={set} key={'bbefore'} side={loops.side} nominal={+loops.beforeNominal} cx={+loops.before+m} cy={height+m-indent} r={radius} m={m} />);
        circles.push(<Circle set={set} key={'bafter'} side={loops.side} nominal={+loops.afterNominal} cx={+loops.after+m} cy={height+m-indent} r={radius} m={m} />);
    }
        

    if(loops.count > 2){
        let steps = loops.count - 1;
        let before = loops.before;
        let after = loops.after;
        let beforeNom = +loops.beforeNominal;
        let afterNom = +loops.afterNominal;
        let stepNom = (afterNom-beforeNom)/steps;
        let step = (after-before)/steps;
        for(let i = 1; i < steps; i++){
            let circleCX = m+indent;
            let circleCY = +loops.before+(step*i)+m;
            let nominal = beforeNom+(stepNom*i)+m;
            if(loops.side === 'top'){
                circleCY = m+indent;
                circleCX = +loops.before+(step*i)+m;
                nominal = beforeNom+(stepNom*i)+m;
            }
            else if(loops.side === 'right'){
                circleCX = width+m-indent;
                circleCY = +loops.before+(step*i)+m;
                nominal = beforeNom+(stepNom*i)+m;
            }
            else if(loops.side === 'bot'){
                circleCY = height+m-indent;
                circleCX = +loops.before+(step*i)+m;
                nominal = beforeNom+(stepNom*i)+m;
            }
            circles.push(
                <Circle 
                    key={`${circleCX}-${circleCY}-${i}`} 
                    nominal={nominal}
                    cx={circleCX} 
                    cy={circleCY} 
                    r={radius} 
                    width={width} 
                    height={height} 
                    side={loops.side}
                    set={set}
                    m={m} />);
        }
    }  
    return circles;
}
  
function Circle(props){
    let set = props.set;
    let side = props.side;
    let line = `M ${props.cx} ${props.cy} h ${props.r + 20}`;
    let numberX = props.cx + 45;
    let numberY = props.cy + 8;
    let number = helper.descalingAny(props.cy,set,side);
    if(side === 'top'){
        line = `M ${props.cx} ${props.cy} v ${props.r + 20}`;
        numberX = props.cx - 23;
        numberY = props.cy + 60;
        number = helper.descalingAny(props.cx,set,side);
    }
    else if(side === 'right'){
        line = `M ${props.cx} ${props.cy} h -${props.r + 20}`;
        numberX = props.cx - 90;
        numberY = props.cy + 10;
        number = helper.descalingAny(props.cy,set,side);
    }
    else if(side === 'bot'){
        line = `M ${props.cx} ${props.cy} v -${props.r + 20}`;
        numberX = props.cx - 20;
        numberY = props.cy - 50;
        number = helper.descalingAny(props.cx,set,side);
    }
    
    let deg = 0;
    if(side === 'top' || side === 'bot'){
        //deg = -90; 
    }

    return (
      <>
      <circle cx={props.cx} cy={props.cy} r={props.r} stroke="#3b8ecc" fill="#3b8ecc" />
      <path d={line}  stroke="#3b8ecc" />
      <text transform={`rotate(${deg},${numberX},${numberY})`} fill="#3b8ecc" fontSize="150%" x={numberX} y={numberY}>{Math.floor(props.nominal)}</text>
      </>
    );
}

export {LoopsOptions, Loops};